import { NextResponse } from "next/server";
import { contract, ensureContractIsDeployed } from "../../lib/blockchain";
import { executeQuery } from "../../lib/db";

export const runtime = "nodejs";

export async function POST(request) {
  try {
    await ensureContractIsDeployed();

    const body = await request.json();
    const { studentId } = body;

    if (!studentId) {
      return NextResponse.json(
        { success: false, error: "studentId is required" },
        { status: 400 }
      );
    }

    const normalizedStudentId = String(studentId).trim();
    const record = await contract.verifyRecord(normalizedStudentId);

    const studentRows = await executeQuery(
      `
        SELECT full_name, program
        FROM students
        WHERE student_id = ?
        LIMIT 1
      `,
      [normalizedStudentId]
    );

    const certificateRows = await executeQuery(
      `
        SELECT cgpa, issued_at, is_revoked
        FROM certificates
        WHERE student_id = ?
        ORDER BY id DESC
        LIMIT 1
      `,
      [normalizedStudentId]
    );

    const student = studentRows[0] || {};
    const certificate = certificateRows[0] || {};

    return NextResponse.json(
      {
        success: true,
        data: {
          studentId: record.studentId,
          fullName: student.full_name || null,
          program: student.program || null,
          cgpa: certificate.cgpa ?? null,
          documentHash: record.documentHash,
          issuer: record.issuer,
          timestamp: record.timestamp.toString(),
          issueDate: certificate.issued_at || null,
          isValid: record.isValid,
          status: record.isValid && !certificate.is_revoked ? "Valid" : "Revoked",
        },
      },
      { status: 200 }
    );
  } catch (error) {
    console.error("Verify API error:", error);

    const message = error.shortMessage || error.message || "Internal server error";
    const isNotFound = message.includes("Record not found");

    return NextResponse.json(
      { success: false, error: message },
      { status: isNotFound ? 404 : 500 }
    );
  }
}
